.navbar {
    background: #ff1c1c;
    height: 70px;
    position: fixed;
    width: 100%;
    -webkit-box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
    color: #fff;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
    z-index: 99;
    white-space: nowrap;
    margin: 0px;
}

.navbar a {
    text-decoration: none;
    color: #fff;
    font-weight: 900;
}

.navbar li {
    display: inline-block;
    padding-top: 17px;
    width: 33.3%;
    text-align: center;
    height: 70px;
}

ul, menu, dir {
    display: block;
    list-style-type: disc;
    padding: 0px;
    height: 70px;
}


